import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { AppFormGroup } from "../shared/common/app.form";

export class PurchaseInvoiceFormGroup extends AppFormGroup {
  static init() {
    return this.getFormGroup();
  }

  static purchaseInvoiceGroup(group: FormGroup) {
    group.addControl(
      "pi_supplier",
      this.getController(true, this.TYPE_DATA, null, null)
    );
    // group.addControl("pi_date",this.getController(true, this.TYPE_DATE, null, 20));
    group.addControl(
      "pi_saleinvoiceno",
      this.getController(true, this.TYPE_DATA, null, 50)
    );
    group.addControl(
      "pi_date",
      this.getController(true, this.TYPE_DATE, null, 20)
    );
  }
}



