import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { DataSource } from "@angular/cdk";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/startWith";
import "rxjs/add/observable/merge";
import "rxjs/add/operator/map";
import { MdSort, MdPaginator } from "@angular/material";
import { RestService } from "app/common/rest.service";
import { FormControl } from "@angular/forms";

export class PurchaseInvoiceDataSource extends DataSource<any> {
  private data: any[] = [];
  //private originaldata: any = null;

  constructor(
    private restService: RestService,
    private searchObject: any,
    private sort: MdSort,
    private paginator: MdPaginator
  ) {
    super();
    this.getDataList();
  }

  getDataList() {
    console.log("serch obj===" + this.searchObject);
    this.restService.showLoader(true);
    this.restService
      .searchPurchaseInvoice(this.searchObject)
      .subscribe(results => {
        if (results) {
          this.data = results;
          this.restService.showLoader(false);
        }
      });
  }

  /** Connect function called by the table to retrieve one stream containing the data to render. */

  connect(): any {
    const displayDataChanges = [
      this.restService.searchPurchaseInvoice(this.searchObject),
      this.sort.mdSortChange,
      this.paginator.page
    ];

    return Observable.merge(...displayDataChanges).map(() => {
      const myData = this.getSortedData();
      // Grab the page's slice of data.
      const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
      return myData.splice(startIndex, this.paginator.pageSize);
    });
  }

  disconnect() {}

  getSortedData(): any[] {
    const data = this.data.slice();
    if (this.sort.active && this.sort.direction == "") {
      return data;
    }
    return data.sort((a, b) => {
      let propertyA: number | string | Date = "";
      let propertyB: number | string | Date = "";
      switch (this.sort.active) {
        case "purchaseInvoiceNumber":
          [propertyA, propertyB] = [a.purchaseInvoiceNo, b.purchaseInvoiceNo];
          break;
        case "saleInvoiceNumber":
          [propertyA, propertyB] = [
            a.supplierInvoiceNumber,
            b.supplierInvoiceNumber
          ];
          break;
        case "invoiceDate":
          [propertyA, propertyB] = [a.invoiceDate, b.invoiceDate];
          break;

        case "totalAmount":
          [propertyA, propertyB] = [
            a.totalPurchaseAmount,
            b.totalPurchaseAmount
          ];
          break;
        case "fromDate":
          [propertyA, propertyB] = [a.fromDate, b.fromDate];
          break;
        case "toDate":
          [propertyA, propertyB] = [a.toDate, b.toDate];
          break;
        //case 'product': [propertyA, propertyB] = [a.productName, b.productName]; break;
        //case 'qty': [propertyA, propertyB] = [a.productPriceListItems.initialStockAtHand, b.productPriceListItems.initialStockAtHand]; break;
      }
      let valueA = isNaN(+propertyA) ? propertyA : +propertyA;
      let valueB = isNaN(+propertyB) ? propertyB : +propertyB;

      return (
        (valueA < valueB ? -1 : 1) * (this.sort.direction == "asc" ? 1 : -1)
      );
    });
  }
}



