import { DialogDeletePurchaseInvoice } from "./dialog-delete-purchase-invoice";
import { AppService } from "./../../../shared/service/app.service";
import { MdSort, MdPaginator, MdDialog } from "@angular/material";
import { Component, OnInit, ViewChild } from "@angular/core";
import { RestService } from "app/common/rest.service";
import { PurchaseInvoiceDataSource } from "./purchase-invoice-listing.datasource";
import { FormControl, FormGroup } from "@angular/forms";
import { Util } from "../../../shared/utils/util";
import { DataSource } from "@angular/cdk";

@Component({
  selector: "app-purchase-invoice-listing",
  templateUrl: "./purchase-invoice-listing.component.html",
  styleUrls: ["./purchase-invoice-listing.component.scss"]
})
export class PurchaseInvoiceListingComponent implements OnInit {
  searchObject: any = {};
  purchaseInvoiceForm: FormGroup;
  //searchField: FormControl;
  selectedItemForView: any = null;
  selectedItem: any = null;
  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;

  dataSource: PurchaseInvoiceDataSource = null;
  displayedColumns: any[] = [
    "purchaseInvoiceNumber",
    "saleInvoiceNumber",
    "invoiceDate",
    "totalAmount",
    "action"
  ];

  constructor(
    private appService: AppService,
    private restService: RestService,
    public dialog: MdDialog
  ) {}

  ngOnInit(): void {
    this.dataSource = new PurchaseInvoiceDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
    this.rangeLoad();
  }

  callDataSource() {
    if (
      this.searchObject.invoiceDate &&
      typeof this.searchObject.invoiceDate == "object"
    ) {
      this.searchObject.invoiceDate = Util.ISODate(
        this.searchObject.invoiceDate.toISOString()
      );
    }
    if (
      this.searchObject.fromDate &&
      typeof this.searchObject.fromDate == "object"
    ) {
      this.searchObject.fromDate = Util.ISODate(
        this.searchObject.fromDate.toISOString()
      );
    }
    if (
      this.searchObject.toDate &&
      typeof this.searchObject.toDate == "object"
    ) {
      this.searchObject.toDate = Util.ISODate(
        this.searchObject.toDate.toISOString()
      );
    }
    this.dataSource = new PurchaseInvoiceDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
  }

  selectItem(item: any) {
    this.selectedItem = item;
  }

  myFun(row) {
    this.selectedItemForView = row;
    console.log(row);
    console.log(this.searchObject);
    this.appService.navigate("./purchaseinvoice/add", [
      { id: this.selectedItemForView.id, isEditable: false }
    ]);
    // if (this.selectedItemForView.productType == "Inventory") {
    //   this.appService.navigate("./admin/inventory-edit", [
    //     { id: this.selectedItemForView.id, isEditable: false }
    //   ]);
    // } else if (this.selectedItemForView.productType == "NonInventory") {
    //   this.appService.navigate("./admin/noninventory-edit", [
    //     { id: this.selectedItemForView.id, isEditable: false }
    //   ]);
    // } else {
    //   this.appService.navigate("./admin/service-edit", [
    //     { id: this.selectedItemForView.id, isEditable: false }
    //   ]);
    // }
  }

  view(item: any) {
    this.appService.navigate("/purchaseinvoice/add", [
      { id: this.selectedItem.id, isEditable: false }
    ]);
  }

  edit(item: any) {
    this.appService.navigate("/purchaseinvoice/add", [
      { id: this.selectedItem.id, isEditable: true }
    ]);
  }

  //For delete Popup
  openDialog(id) {
    const dialogRef = this.dialog.open(DialogDeletePurchaseInvoice, {
      width: "500px",
      height: "250px",
      data: id
    });

    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }

  // download() {
  //   this.restService.purchaseInvoiceDownload(this.selectedItem.id);
  // }
  public clearAll() {
    this.searchObject = {};
    this.dataSource = new PurchaseInvoiceDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
  }
  delete(item: any) {
    this.openDialog(this.selectedItem.id);
  }
  rangeList: any[] = [];
  rangeLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.rangeList = results["Range"];
      });
  }
}



