import { PurchaseInvoiceListingComponent } from './purchase-invoice-listing/purchase-invoice-listing.component';
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { CreatePurchaseInvoiceComponent } from './create-purchase-invoice/create-purchase-invoice.component';

const routes: Routes = [
  { path: "add", component: CreatePurchaseInvoiceComponent },
  {path:"search",component:PurchaseInvoiceListingComponent}  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PurchaseInvoiceRoutingModule {}



