import { AppService } from "app/shared/service/app.service";
import { PurchaseInvoiceFormGroup } from "./../../../forms/purchaseinvoice.form";
import { PurchaseInvoice } from "./../../../entities/purchaseinvoice.entity";
import { Component, OnInit, SimpleChanges } from "@angular/core";
import { StockItem } from "app/entities/stockitem.entity";
import { RestService } from "app/common/rest.service";
import { PurchaseInvoiceItem } from "app/entities/purchaseinvoiceitem.entity";

@Component({
  selector: "app-create-purchase-invoice",
  templateUrl: "./create-purchase-invoice.component.html",
  styleUrls: ["./create-purchase-invoice.component.scss"]
})
export class CreatePurchaseInvoiceComponent implements OnInit {
  purchaseInvoiceForm: any = PurchaseInvoiceFormGroup.init();
  // purchaseStockItemForm: any = PurchaseInvoiceFormGroup.init();
  purchaseInvoice: PurchaseInvoice;

  supplierList: any[] = [];
  filteredSuppliers: any[] = [];
  supplierSelected: string = "";

  itemSelected: PurchaseInvoiceItem;
  stockItemList: any[] = [];
  filteredStockItems: any[] = [];

  isEditable: string = "true";
  isEdit: boolean = false;

  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    this.purchaseInvoice = new PurchaseInvoice();
    this.purchaseInvoice.purchaseSubTotal = 0.0;
    this.purchaseInvoice.purchaseTaxAmount = 0.0;
    this.purchaseInvoice.totalPurchaseAmount = 0.0;
    this.purchaseInvoice.isTaxable = 0;
    this.purchaseInvoice.companyId = this.appService.getBranch().id;
    PurchaseInvoiceFormGroup.purchaseInvoiceGroup(this.purchaseInvoiceForm);
  }

  ngOnInit() {
    this.init();

    let id = this.appService.getParam("id");
    this.isEditable = this.appService.getParam("isEditable");
    if (id !== undefined) {
      this.retrievePurchaseInvoice(id);
    } else {
      this.isEdit = false;
      this.isEditable = "true";
      this.addStockItem();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    this.purchaseInvoice = changes["purchaseInvoice"].currentValue;
  }

  init() {
    this.loadSuppliers();
    this.loadStockItems();
  }

  retrievePurchaseInvoice(id: string) {
    // this.restService.dataloadFromFile("./assets/json/tempdata/purchaseinvoices.json")
    // .subscribe(results => {
    //   if (results) {
    //     let purchaseInvoices: PurchaseInvoice[] = results["data"];
    //     this.purchaseInvoice = purchaseInvoices.find(a => a.id == id);
    //     this.purchaseInvoice.companyId = this.appService.getBranch().id;
    //     let supplierFound: any = this.supplierList.find(c => c.id == this.purchaseInvoice.supplierId);
    //     this.supplierSelected = supplierFound.companyName + "-"+supplierFound.mobileNumber;
    //     this.isEdit = true;
    //     console.log(this.purchaseInvoice);
    //   }
    // });

    this.restService.getPurchaseInvoice({ id: id }).subscribe(results => {
      if (results) {
        this.purchaseInvoice = results;
        this.purchaseInvoice.companyId = this.appService.getBranch().id;
        // let supplierFound: any = this.supplierList.find(
        //   c => c.id == this.purchaseInvoice.supplierId
        // );
        this.supplierSelected =
          this.purchaseInvoice.supplier.companyName +
          "  -  " +
          this.purchaseInvoice.supplier.mobileNumber;
        this.purchaseInvoice.supplierId = this.purchaseInvoice.supplier.id;
        this.isEdit = true;
        console.log(this.purchaseInvoice);
        this.addStockItem();
      }
    });
  }

  loadSuppliers() {
    // this.restService
    //   .dataloadFromFile("./assets/json/tempdata/suppliers.json")
    //   .subscribe(results => {
    //     if (results) {
    //       this.supplierList = results["data"];
    //     }
    //   });

    this.restService.searchSupplier({}).subscribe(results => {
      if (results) {
        this.supplierList = results;
      }
    });
  }

  loadStockItems() {
    // this.restService
    //   .dataloadFromFile("./assets/json/tempdata/stockitems.json")
    //   .subscribe(results => {
    //     if (results) {
    //       this.stockItemList = results["data"];
    //     }
    //   });

    this.restService
      .getStockItems({ id: this.appService.getBranch().id })
      .subscribe(results => {
        this.stockItemList = results;
      });
  }

  filterSupplier(val: string) {
    if (this.isEditable == "true") {
      this.filteredSuppliers =
        val && val != ""
          ? this.supplierList.filter(
              s =>
                s.companyName.toLowerCase().indexOf(val.toLowerCase()) >= 0 ||
                s.mobileNumber.toLowerCase().indexOf(val.toLowerCase()) >= 0
            )
          : this.supplierList;
    } else {
      this.filteredSuppliers = [];
    }
  }

  filterStockItems(val: string) {
    if (this.isEditable == "true") {
      this.filteredStockItems =
        val && val != ""
          ? this.stockItemList.filter(
              s => s.productName.toLowerCase().indexOf(val.toLowerCase()) >= 0
            )
          : this.stockItemList;
    } else {
      this.filteredStockItems = [];
    }
  }

  selectedSupplier(item: any) {
    console.log(item);
    if (item && typeof item == "object") {
      this.supplierSelected = item.companyName + "-" + item.mobileNumber;
      this.purchaseInvoice.supplierId = item.id;
    } else if (!isNaN(item)) {
      this.supplierSelected = "";
    } else {
      this.supplierSelected = "";
    }
  }

  addStockItem() {
    if (
      this.purchaseInvoice.purchaseInvoiceStockItemList.length == 0 ||
      this.purchaseInvoice.purchaseInvoiceStockItemList[
        this.purchaseInvoice.purchaseInvoiceStockItemList.length - 1
      ].stockItem.id != null
    ) {
      let item = new PurchaseInvoiceItem();
      item.id = null;
      item.qty = 1;
      item.itemTotal = 0.0;
      item.taxInclusive = "true";
      this.purchaseInvoice.purchaseInvoiceStockItemList.push(item);
    }
  }

  selectLineItem(lineItem, index) {
    if (this.itemSelected !== lineItem) {
      this.itemSelected = lineItem;
    }
  }
  onKeydown(event) {
    console.log(event);
  }

  selectStockItem(item: any) {
    console.log(item);
    this.itemSelected.stockItem.id = item.id;
    this.itemSelected.taxInclusive = "false";
    this.itemSelected.stockItem.productName = item.productName;
    this.itemSelected.rate = item.costPrice;
    this.itemCalculate(this.itemSelected);
    //this.addStockItem();
    this.calculateTotal();
  }

  itemValueChange(item: any, field: string) {
    this.itemCalculate(item);
    this.calculateTotal();
    if (field == "r") {
      this.addStockItem();
    }
  }

  itemCalculate(item: any) {
    item.itemTotal = item.qty * item.rate;
  }

  calculateTotal() {
    let total = 0;
    this.purchaseInvoice.purchaseInvoiceStockItemList.forEach(element => {
      let qty = element.qty;
      let price = element.rate != null ? element.rate : 0.0;
      console.log(element);
      total += qty * price;
    });
    this.purchaseInvoice.purchaseSubTotal = total;
    this.purchaseInvoice.totalPurchaseAmount =
      total + this.purchaseInvoice.purchaseTaxAmount;
  }
  done() {
    this.appService.navigate("/purchaseinvoice/search", []);
  }

  removeItem(index) {
    this.purchaseInvoice.purchaseInvoiceStockItemList[index].stockItem = null;
    if (this.purchaseInvoice.purchaseInvoiceStockItemList.length > 0) {
      setTimeout(() => {
        this.purchaseInvoice.purchaseInvoiceStockItemList.splice(index, 1);
        this.calculateTotal();
      }, 10);
    }
  }

  isValidPurchaseInvoice() {
    const first = this.purchaseInvoice.purchaseInvoiceStockItemList[0];
    if (first && first.stockItem) {
      return first.qty > 0 && first.rate > 0;
    }
    return false;
  }

  createPurchaseInvoice() {
    let filteredItems: PurchaseInvoiceItem[] = this.purchaseInvoice.purchaseInvoiceStockItemList.filter(
      s => s.stockItem.id != "undefined" && s.stockItem.id != null
    );
    this.purchaseInvoice.purchaseInvoiceStockItemList = filteredItems;
    console.log(JSON.stringify(this.purchaseInvoice));
    this.restService
      .savePurchaseInvoice(this.purchaseInvoice)
      .subscribe(results => {
        let message = "-----------------";
        if (this.isEdit) {
          message = "Purchase Invoice updated successfully.";
          //this.expenseManagement = result;
        } else {
          message = "Purchase Invoice created successfully.";
        }
        this.appService.showMessage(message);
        this.appService.navigate("purchaseinvoice/search", []);
      });
  }
  isReadOnlyC(item, isEditable) {
    if (isEditable == "true") {
      console.log("isEditable is ", isEditable);
      console.log("Stock is  ", item.stockItem.id);
      if (item.stockItem.id == null) {
        console.log("Stock is  ", isEditable);
        return true;
      } else {
        return false;
      }
    } else {
      console.log("isEditable is ", isEditable);
      return true;
    }
  }
  onlyNumberKey(event) {
    return event.charCode == 8 || event.charCode == 0
      ? null
      : event.charCode >= 48 && event.charCode <= 57;
  }
}



