import { DialogDeletePurchaseInvoice } from './purchase-invoice-listing/dialog-delete-purchase-invoice';
import { PurchaseInvoiceListingComponent } from './purchase-invoice-listing/purchase-invoice-listing.component';
import { SharedModule } from './../../shared/shared.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CreatePurchaseInvoiceComponent } from './create-purchase-invoice/create-purchase-invoice.component';
import { PurchaseInvoiceRoutingModule } from './purchaseinvoice-routing.module';

@NgModule({
  imports: [
    CommonModule,PurchaseInvoiceRoutingModule, SharedModule
  ],
  declarations: [
    CreatePurchaseInvoiceComponent, 
    PurchaseInvoiceListingComponent,
    DialogDeletePurchaseInvoice
  ],
  entryComponents: [
    DialogDeletePurchaseInvoice
  ]  
})
export class PurchaseInvoiceModule { }



